 
*{
    box-sizing: border-box;
}



body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: coral;
    height: 100%;
}

html, 
body, 
#potato,
.container {
    height : 100%;
    display: flex;
    justify-content: center;
    
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 1000;
    font-size: 50px;
}
